@import "../../theme/colors";

.personal-project {
  text-align: start;
  padding: 16px;
  box-sizing: border-box;
  color: $primary_color !important;
  min-height: 110px;
  width: 100%;
  h2 {
    position: relative;
    margin: 0;
    svg {
      position: absolute;
      right: 0;
      bottom: 5px;
    }
  }
}